
import { Box, ListItem, Typography } from "@material-ui/core";
import * as React from "react";

export default function TwoRow(/** @type {{label: string, value: string, strong?: boolean, divider?: boolean }}*/{ label, value, strong = false, divider = false }) {
    return <ListItem divider={divider}>
        <Typography variant={strong ? "h6" : "body1"}>
            {label}
        </Typography>
        <Box flexGrow={1} minWidth="10px" />
        <Typography align="right" variant={strong ? "h6" : "body1"} style={{ wordBreak: 'break-word', whiteSpace: 'break-spaces' }}>
            {value || '-'}
        </Typography>
    </ListItem>
}